
@media (max-width: 767px){
.content-wrapper{
    margin-top: 100px !important;
}
}
.content-wrapper{
  margin-top: 50px;
  min-height: 100vh;
}
.page-enter-active, .page-leave-active {
  transition: opacity 0.5s, transform 0.5s;
}
.page-enter, .page-leave-to {
  opacity: 0;
}
.uk-footer {
  bottom: 0;
  position: absolute !important;
  width: 100%;
  padding: 5px;
}



